* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  background-image: url(../img/106394637-abstract-many-lines-and-points-background-geometry-wallpaper-vector-design-connection-concept.webp);
  background-size: cover;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
#search-box:hover,
#search-box:focus {
  outline: 2px solid slateblue;
}
.country-list {
  list-style: none;
}
/* js-many */
.group {
  display: grid;
  grid-template-columns: 0.3fr 4fr;
}
.span {
  font-weight: 700;
}
